import { createStore } from 'vuex'
import json from '../assets/questions-emprise-mentale-v1.json'


export default createStore({
  state: {
    title: json.title,
    questions: json.questions,
    currentQuestionIndex: 0,
    possibleAnswers: json.possibleAnswers,
    answers: [],
    possibleResults: json.possibleResults
  },
  mutations: {
    select(state, answer) {
      state.answers.push(answer)
      if(state.currentQuestionIndex < state.questions.length) {
        state.currentQuestionIndex++;
      }
    },
    reset(state) {
      state.currentQuestionIndex = 0;
      state.answers = [];
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {
    currentQuestion: state => state.questions[state.currentQuestionIndex],
    result: state => {
      if(state.answers.length === 0) {
        return { score: null, description: "Aucun résultat" };
      }

      const score = state.answers.map(a => a.value).reduce((x, y) => x + y);

      return state.possibleResults.filter(r => r.score <= score).slice(-1)[0];
    }
  }
})
