<template>
  <div class="hello">
    <div v-if="id <= count">
      <h1>Question {{ id }} / {{ count }}</h1>
      <h3 class="label">{{ title }}</h3>
      <ul>
        <li @click="select(answer)" v-for="(answer, index) in answers" :key="index" :value="answer.value">{{ answer.text }}</li>
      </ul>
    </div>
    <div v-else>
      <div>{{ result.description }}</div>
      <div @click="reset" class="button">Repasser le test</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'QuestionCard',
  props: {
    // title: String,
  },
  computed: {
      id() { return this.$store.state.currentQuestionIndex + 1 },
      count() { return this.$store.state.questions.length },
      title() { return this.$store.getters.currentQuestion },
      answers() { return this.$store.state.possibleAnswers },
      result() { return this.$store.getters.result },
  },
  methods: {
    select(answer) {
      this.$store.commit('select', answer)
    },
    reset() {
      this.$store.commit('reset')
    }
  }
}
</script>

<style scoped lang="scss">
.label {
  margin: auto;
  max-width: 600px;
}

h3 {
  font-weight: 500;
  font-size: 1.1em;
}

ul {
  list-style-type: none;
  padding: 0;
  align-content: center;
  max-width: 90%;
  margin: auto;
}

li {
  max-width: 400px;
  padding: 10px;
  margin: 10px auto;
  cursor: pointer;
  background-color: #f3f3f3;

  &:hover {
    background-color: #E1F5FE;
    font-weight: 600;
  }
}

.button {
  background-color: white;
  border: 2px solid #4CAF50;
  color: black;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  margin: 20px 2px 4px;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  transition-duration: 0.4s;

  &:hover {
    background-color: #4CAF50;
    color: white;
  }
}
</style>
